const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			collectionList:[],
			imgUrl:'',
			is_member: '',
			token: ''
		}
	},
	onLoad() {
		this.token = uni.getStorageSync('token')
		this.getList();
		this.imgUrl = helper.imgUrl
	},
	methods: {
		// collectionList
		getList(){
			console.log(this.token)
			uni.request({
				url: `${helper.requestUrl}/system-tools/apps`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == 200) {
						this.collectionList = res.data.data
						this.is_member = res.data.is_member
					} else {
						// uni.showToast({
						// 	title: res.data.message
						// });
					}
			
				}
			})
		},
		go(e){
			let type = e.currentTarget.dataset.type
			let extra = e.currentTarget.dataset.extra
			let id = e.currentTarget.dataset.id
			let name = e.currentTarget.dataset.name
			console.log(type, extra, id, name)
			if(type == 'block'){
				if (extra != '') {
					if(name == '会员区别'){
						uni.navigateTo({
							url:`/pages/articleDetail?id=189`
						})
					} else if(name == '推广返佣'){
						if(this.is_member == 1){
							// 会员
							uni.navigateTo({
								url:`/pages/${extra}`
							})
						}else{
							uni.showToast({
								title: '您不是会员，暂无此权限',
								icon: "none",
								duration: 2000
							})
						}
					}else{
						uni.navigateTo({
							url:`/pages/${extra}`
						})
					}
					
				} else {
					uni.showToast({
						title: '此页面不存在'
					});
				}
			} else if(type == 'series'){
				
			} else if(type == 'post'){
				uni.navigateTo({
					url:`/pages/articleDetail?id=${id}`
				})
				// 帖子
			} else if(type == 'child'){
				// 应用子
				console.log(id)
				uni.navigateTo({
					url:`/pages/applyShow?id=${id}&name=${name}`
				})
			} else if(type == 'ex_link'){
				if(extra == ''){
					uni.showToast({
						title: '此页面不存在',
						icon: 'none'
					})
				}else{

							uni.showToast({
								title:'该小程序在不支持，请下载App',
								icon: 'none'
							})








				}
				// plus.runtime.openURL(extra)
				// window.location.href = extra
				// 外联
			} else if(type == 'category'){
				uni.navigateTo({
					url:`/pages/applyShow?id=${id}&name=${name}`
				})
			}
			console.log(e)
		}
	}
}
